.ButtonWithUndo {
  background-color: green;
  width: 100px;
  height: 20px;
  background-size: 300% auto;
  transition: 3s;
  background-image: linear-gradient(
    to right,
    #0b8500 0%,
    #089600 35%,
    #ffc400 70%,
    #ffc400 100%
  );
}

.ButtonWithUndo.undo {
  background-position: right center;
}
